import * as React from "react";
import {useOnScreen} from "../../viewport/IsInViewport";
import useIsClient from "../../../hooks/useIsClient";

import moment from "moment";

import "./styles.scss";

const AnimatedCard = ({ animation, digit }) => {
    return (
        <div className={`flipCard ${animation}`}>
            <span>{digit}</span>
        </div>
    );
};

const StaticCard = ({ position, digit }) => {
    return (
        <div className={position}>
            <span>{digit}</span>
        </div>
    );
};

const FlipUnitContainer = ({ digit, shuffle, unit }) => {
    let currentDigit = digit;
    let previousDigit = digit + 1;

    if (unit === 'seconds' || unit === 'minutes') {
        currentDigit = parseInt(currentDigit) === 0
            ? 60
            : currentDigit;
    } else if (unit === 'hours') {
        currentDigit = parseInt(currentDigit) === 0
            ? 24
            : currentDigit;
    }

    // add zero
    if (currentDigit < 10) {
        currentDigit = `0${currentDigit}`;
    }
    if (previousDigit < 10) {
        previousDigit = `0${previousDigit}`;
    }

    // shuffle digits
    const digit1 = shuffle
        ? previousDigit
        : currentDigit;
    const digit2 = !shuffle
        ? previousDigit
        : currentDigit;

    // shuffle animations
    const animation1 = shuffle
        ? 'fold'
        : 'unfold';
    const animation2 = !shuffle
        ? 'fold'
        : 'unfold';

    return (
        <div>
            <div>{unit.charAt(0).toUpperCase() + unit.slice(1)}</div>
            <div className={'flipUnitContainer'}>
                <StaticCard
                    position={'upperCard'}
                    digit={currentDigit}
                />
                <StaticCard
                    position={'lowerCard'}
                    digit={previousDigit}
                />
                <AnimatedCard
                    digit={digit1}
                    animation={animation1}
                />
                <AnimatedCard
                    digit={digit2}
                    animation={animation2}
                />
            </div>
        </div>
    );
};

const Countdown = (props) => {
    const ref = React.useRef();
    const onScreen = useOnScreen(ref);

    const [time, setTime] = React.useState({
        days: {
            value: 0,
            shouldShuffle: true,
        },
        hours: {
            value: 0,
            shouldShuffle: true,
        },
        minutes: {
            value: 0,
            shouldShuffle: true,
        },
        seconds: {
            value: 0,
            shouldShuffle: true,
        }
    });

    const eta = moment.unix(parseInt(props.eta));

    React.useEffect(() => {
        const interval = setInterval(() => {
            if (onScreen) {
                const delta = moment.duration(eta.diff(moment.now()));

                const timePortion = ['days', 'hours', 'minutes', 'seconds'];
                let parts = [];
                for (const part of timePortion) {
                    let d = 0;
                    switch(part) {
                        case 'days':
                            d = delta.asDays();
                            break;
                        case 'hours':
                            d = delta.asHours();
                            break;
                        case 'minutes':
                            d = delta.asMinutes();
                            break;
                        case 'seconds':
                            d = delta.asSeconds();
                            break;
                        default:
                            break;
                    }
                
                delta.subtract(moment.duration(Math.floor(d), part));
                parts.push(Math.floor(d));
                }

                const nextTime = parts.reduce((accum, value, idx) => {
                    const timeUnit = timePortion[idx];
                    const shouldShuffle = time[timeUnit].value !== value ;
                    return {
                        ...accum,
                        [timeUnit]: {
                            value: value,
                            shouldShuffle
                        }
                    };
                }, {});

                setTime(nextTime);
            }
        }, 30);

        return () => clearInterval(interval);
    }, [onScreen, time, eta, setTime]);


    if (eta <= moment.now()) {
        return (<></>);
    }

    return (
        <div ref={ref} className={'FlipClock'}>
            {Object.keys(time).map(key => {
                const {value, shouldShuffle} = time[key];
                return (
                    <FlipUnitContainer
                        key={key}
                        unit={key}
                        digit={value}
                        shuffle={shouldShuffle}
                    />
                );
            })}
        </div>
    );
}


const CountdownRenderer = (props) => {
    const {isClient} = useIsClient();

    if (!isClient) {
        return null; 
    }
    return <Countdown {...props} />
}

export default CountdownRenderer;