import * as React from "react";
import { Link as ExternalLink } from '@chakra-ui/react';
import { Link as GatsyLink } from "gatsby";
import ParseUrl from "url-parse";

const okDomains = new Set([
    "localhost",
    "anthonyandmaiwang.github.io",
    "anthonyandmaiwang",
]);

const isCurDomain = (href) => {
    const url = new ParseUrl(href);
    return okDomains.has(url.hostname);
}

function detectURLs(message) {
    const urlRegex = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?!&//=]*)/gi;
    return message.match(urlRegex);
}

const ThemedLink = (props) => {
    if (!props.href) {
        return null;
    }

    const urls = detectURLs(props.href);
    const url = urls && urls.length > 0 ? urls[0] : props.href;
    if (isCurDomain(url)) {
        return (
            <GatsyLink className="chakra-link css-c6nly4" to={url}>{props.children}</GatsyLink>
        );
    }

    return (
        <ExternalLink isExternal color="teal.500" href={url}>{props.children} </ExternalLink>
    );
}

export default ThemedLink;