import * as React from "react";
import {getImagesFromDoc} from "../../image/getImagesFromDoc";

// Import Swiper React components
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';

import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

const CustomImages = (props) => {
  const childrenImgs = getImagesFromDoc(props).map(img => {
    const newProps = {...img.props};
    delete newProps.style;

    newProps.style = {
      maxWidth: '100%',
      maxHeight: "300px"
    }
    return {
      ...img,
      props: newProps
    };
  });

  return (
    <Swiper
      modules={[Navigation, Pagination, Scrollbar, A11y]}
      spaceBetween={50}
      slidesPerView={3}
      navigation={true}
      pagination={{ clickable: true }}
      scrollbar={{ draggable: true }}
    >
      {childrenImgs.map((img, key) => {
        return <SwiperSlide key={key}>{img}</SwiperSlide>
      })}

      ...
    </Swiper>
  );
}

export default CustomImages;