
const getImages = (props) => {
    if (typeof props !== 'object') {
        return [];
    }

    let imgs = [];

    if (props.children) {
        props.children.forEach((child) => {
            if (typeof child === 'object') {
                const type = typeof child.type === "function" ? child.type().type : child.type;

                if (type === 'img' || type === "Image") {
                    imgs = [...imgs, child];
                } else {
                    const res = getImages(child.props);
                    imgs = [...imgs, ...res];
                }
            }
        });
    }

    return imgs;
}


export const getImagesFromDoc = (props) => {
    return getImages(props).map(img => {
        const newProps = {...img.props};
        delete newProps.style;
        return {
            ...img,
            props: newProps
          };
    });
}
