import React from "react";
import Link from "../Link";

import {loadStripe} from "@stripe/stripe-js"

const test_key = "pk_test_51LPXNVJoFlcbjIz35ixglaeaQ26M9KkjuTkHkCpvWSvb12m1YewZ6RYOQ2xqt21MpMFqjVMziEgjtKoLzB46X0Dm00Ks33EUmx",
    live_key = "pk_live_51LPXNVJoFlcbjIz3ynp0bHIuERO3ykxTAjggynQDZU2kHTzOt9TuWflEvSZtkMeqoGQ4QH13HLdV6630SHz11TFN00Ns1uYwoR";

const StripeProduct = (props) => {
    const {id, link, test} = props;
    if (link) {
        const normalizedLink = link.replaceAll('\\', '');
        return (
            <div>
                {props.children}
                <Link href={normalizedLink}>Purchase here</Link>
            </div>
        );
    }

    const product = {
        price: id.replaceAll('\\', ''),
        quantity: 1,
    };

    const checkoutOptions = {
        mode: "payment",
        lineItems: [product],
        successUrl: `https://anthonyandmaiwang.github.io/lebirb/success`,
        cancelUrl: `https://anthonyandmaiwang.github.io/lebirb/products`,
    }

    const redirectToCheckout = async () => {
        const key = Boolean(test) === true ? test_key : live_key;
        const stripe = await loadStripe(key);

        await stripe.redirectToCheckout(checkoutOptions);
    };


    return (
        <div>
            {props.children}
            <button onClick={redirectToCheckout}>Buy Water Bottle</button>
        </div>
    );
};

export default StripeProduct;