import * as React from "react";
import Lottie from "lottie-react";
import {useOnScreen} from "../../viewport/IsInViewport";

const lottieOptions = {
    loop: true,
    autoplay: false,
    rendererSettings: {
        preserveAspectRatio: 'xMidYMid slice',
    }
};

export const LottieWrapper = (props) => {
    const containerRef = React.useRef(),
        lottieRef = React.useRef(),
        onScreen = useOnScreen(containerRef),
        {animationData, style} = props;

    React.useEffect(() => {
        if (onScreen) {
            lottieRef.current.play();
        } else {
            lottieRef.current.pause();
        }
    }, [onScreen]);

    const options = {
        animationData,
        ...{
            ...lottieOptions,
            autoplay: onScreen,
        },
    };

    return (
        <div ref={containerRef}>
            <Lottie
                lottieRef={lottieRef} 
                className="LottieAnimation"
                {...options}
                style={style}
                {...props}
            />
        </div>
    );
};

export default LottieWrapper;