import * as React from "react";
import { H3 } from "../Header";
import Link from "../Link";

import {getImagesFromDoc} from "../../image/getImagesFromDoc";

import "./styles.scss";

const RegistryItem = (props) => {

    const childrenImgs = getImagesFromDoc(props).map(img => {
        const newProps = {...img.props};
        delete newProps.style;
    
        newProps.style = {
          maxWidth: '100%',
          maxHeight: "200px"
        };
        return {
          ...img,
          props: newProps
        };
    });

    return (
        <div className="RegistryItem">
            <H3 className="RegistryItemTitle" size='md' noOfLines={1}>{props.title}</H3>

            <div className="RegistryItemPhotoContainer">
                {childrenImgs}
            </div>

            <div className="CTAContainer">
                <Link href={props.learnmore}>Learn More</Link><Link href={props.link}>Buy</Link>
            </div>
        </div>
    );
}

export default RegistryItem;