import React from "react";
import { graphql, useStaticQuery } from "gatsby";
import WeddingNavBar  from "./components/weddingNavbar";
import BasicNavBar  from "./components/basicNavbar";

export const Menu = (props) => {
    const { curRoute, route } = props;
    const data = useStaticQuery(graphql`
        query {
            weddingMenu: googleDocs(name: {eq: "WeddingMenu"}) {
                childrenMarkdownRemark {
                    html
                    htmlAst
                }
            }
            defaultMenu: googleDocs(name: {eq: "Menu"}) {
                childrenMarkdownRemark {
                    html
                    htmlAst
                }
            }
        }
    `);

    let menu;
    if (route === "WeddingMenu") {
        menu = data.weddingMenu;
    } else {
        menu = data.defaultMenu;
    }

    if (route === "WeddingMenu") {
        return (
            <WeddingNavBar 
                curRoute={curRoute}
                htmlAst={menu.childrenMarkdownRemark[0].htmlAst} />
            ); 
    }

    return (
        <BasicNavBar 
            curRoute={curRoute}
            htmlAst={menu.childrenMarkdownRemark[0].htmlAst} />
        ); 
};

export default Menu;